.headerTitle {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-content: center;

    height: 1.5rem;
    background-color: #003220;
    color: whitesmoke;
    font-size: 1rem;
}
.logoBox {
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    background-color: rgb(25, 25, 45);
    color: whitesmoke;
    width: 100%;
    height: 6rem;
}
nav{
    background-color: rgb(233, 233, 200);
}

.mLetter {
    color: #009933;
    font-size: 2rem;
    font-weight: bold;

    margin-left: 1rem;
}
.hLetter{
    /*color: #003220;*/
    color: whitesmoke;
    font-size: 2rem;
    font-weight: bold;
    margin-left: 0.1rem;
}

.navigationUl {
    display:flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-content: center;
    
    list-style: none;
}

.link {
    margin: 0.4rem;
    border-radius: 5px;
    font-weight: bold;
}

a {
    text-decoration: none;
}
a:link {
    color: black;
}
a:visited {
    color: black;
}
a:hover {
    color: #009933;
}

a:active {
 background-color: rgb(209, 164, 164);
}